import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class PedidoDataService {

  private contaSource = new BehaviorSubject(0);
  currentConta = this.contaSource.asObservable();

  constructor() { }

  changeContaAdd(valor: number){
    let acumulate = this.contaSource.getValue();
    acumulate += valor;

    this.contaSource.next(acumulate);
  }

  changeContaRmv(valor: number){
    let acumulate = this.contaSource.getValue();
    acumulate -= valor;

    this.contaSource.next(acumulate);
  }
}
