import { Component, Inject, OnInit } from '@angular/core';

import {MatDialogRef, MAT_DIALOG_DATA, MatSnackBarConfig, MatSnackBar, MatStepper} from '@angular/material';
import { DialogData } from 'src/app/models/dialogData';
import { FormGroup, Validators, FormBuilder, NgControlStatus  } from '@angular/forms';
import { Produto } from 'src/app/models/produto';


@Component({
  selector: 'app-dialog-adicionar-produto',
  templateUrl: './dialog-adicionar-produto.component.html',
  styleUrls: ['./dialog-adicionar-produto.component.css']
})
export class DialogAdicionarProdutoComponent implements OnInit{

  checked = false;
  isLinear = true;
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  desabilitaBtnOK = false;

  ngOnInit() {
    this.firstFormGroup = this._formBuilder.group({
      //firstCtrl: ['', Validators.required]
      firstCtrl: ['']
    });
    this.secondFormGroup = this._formBuilder.group({
      secondCtrl: ['', Validators.required]
    });
  }

  constructor(
    private _formBuilder: FormBuilder,
    public snackBar: MatSnackBar,
    public dialogRef: MatDialogRef<DialogAdicionarProdutoComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) {}

  onCancelClick(): void {
    this.dialogRef.close('cancel');
  }

  onOkClick(): void {    
    this.data.adicionar = true;

    this.dialogRef.close(this.data);
  }

  onChooseSegundoSabor(segundoSaborEscolhido: Produto, stepper: MatStepper){
    let nome = this.data.nomeProduto;
    this.data.nomeProduto = '1/2 ';
    this.data.nomeProduto += nome;
    this.data.nomeProduto += ' + 1/2 ';
    this.data.nomeProduto += segundoSaborEscolhido.nome;
    stepper.next();

    this.calcularPreco2Sabores(segundoSaborEscolhido.preco);
    
    this.desabilitaBtnOK = false;
  }

  onChangeQuantidade(){
    if( this.data.observacao != null){      
      this.openSnackBar('Aten칞칚o! A observa칞칚o inserida servir치 para todos os '+ this.data.quantidade +' itens. \n' +'"'+ this.data.observacao+'"');
    }
    
  }

  iniciaRotina2Sabores(){
    if(this.checked == true){
      this.desabilitaBtnOK = false;
    } else {
      this.desabilitaBtnOK = true;
    }
  }

  calcularPreco2Sabores(precoSegundoSabor: number){
    if(this.data.produto2Sabores.categoria.precoMedio2Sabores == true){
      this.data.precoCalculado = Math.round(((this.data.produto2Sabores.preco + precoSegundoSabor) / 2) * 100)/100;
    } else {
      
      if(this.data.produto2Sabores.preco > precoSegundoSabor){
        this.data.precoCalculado = this.data.produto2Sabores.preco;       
      } else {
        this.data.precoCalculado = precoSegundoSabor;       
      }
    }
  }

  openSnackBar(message: string) {
    const config = new MatSnackBarConfig();
    config.panelClass = ['custom-snackbar'];
    config.duration = 7000;

    this.snackBar.open(message, 'Ok', config);
  }
}
