import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Pedido } from './models/pedido';
import { Cardapio } from './models/cardapio';
import { catchError, retry } from 'rxjs/operators';
import { throwError, Observable } from 'rxjs';



@Injectable({
  providedIn: 'root'
})

export class PedidosService {

  

  //cardapioUrl = 'http://192.168.1.113:8080/buscarCardapio?cliente=5ba717ae0cced02530cd403f&mesa=01';
  //enviarPedidoUrl = 'http://192.168.1.113:8080/enviarPedido';

  //PROD
  cardapioUrl = '/buscarCardapio?cliente=5ba83d9ac954ef000429345a&mesa=01';
  enviarPedidoUrl = '/enviarPedido';

  //cardapioUrl = 'https://epizza-demo.herokuapp.com/buscarCardapio?cliente=5ba83d9ac954ef000429345a&mesa=01';
  //enviarPedidoUrl = 'https://epizza-demo.herokuapp.com/enviarPedido';
  
  constructor( private http: HttpClient ) { }

  buscarCardapio(): Observable<Cardapio>{
      return this.http.get<Cardapio>(`${this.cardapioUrl}`) 
          .pipe(
            retry(2),
            catchError(this.handleError)
          );             
  }

  enviarPedido(pedido: Pedido){
    return this.http.post<Pedido>(this.enviarPedidoUrl, pedido);
  } 

  private handleError(error: HttpErrorResponse){
    if( error.error instanceof ErrorEvent){
      console.error('Client Side Error: ', error.error.message);
    } else {
      console.error('Server Side Error: ', error);
    }

    return throwError('N칚o foi poss칤vel buscar o card치pio, verifique sua conex칚o com a internet e tente novamente.');
  }

}

