import { Component, Input, OnInit} from '@angular/core';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { Produto } from '../models/produto';
import { Pedido } from '../models/pedido';
import { PedidoDataService } from '../services/pedido-data.service';
import { PedidosService } from '../pedidos.service';

@Component({
  selector: 'app-main-nav',
  templateUrl: './main-nav.component.html',
  styleUrls: ['./main-nav.component.css']
})
export class MainNavComponent implements OnInit  {
  

  @Input()
  pedidoBar: Pedido;
  minhaConta: number;
  retornoEnviarPedido: any;
  pedidosEnviados: Pedido;

  isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
    .pipe(
      map(result => result.matches)
    );
  
  constructor(private breakpointObserver: BreakpointObserver, private pedidoDataService: PedidoDataService, private pedidosService: PedidosService) {}

  ngOnInit() {
    this.pedidoDataService.currentConta.subscribe(valor => this.minhaConta = valor);
  }  

  removerProduto(produtoRemovido: Produto){    
    for(let i = 0; i < this.pedidoBar.produtos.length; i++){
      if(this.pedidoBar.produtos[i].id == produtoRemovido.id && this.pedidoBar.produtos[i].quantidade == produtoRemovido.quantidade && this.pedidoBar.produtos[i].observacao == produtoRemovido.observacao){
        this.pedidoBar.produtos.splice(i, 1);
      }
    }
    
    this.pedidoDataService.changeContaRmv(produtoRemovido.preco);
  }

  enviarPedido(){
    this.pedidosService.enviarPedido(this.pedidoBar).subscribe(retorno => this.retornoEnviarPedido = retorno );
    let addPedidoEnviado: Pedido = this.pedidoBar;
    this.pedidosEnviados = addPedidoEnviado;
    this.pedidoBar.produtos = [];
    
    alert('Seu pedido foi enviado!');
  }
}
